<template>
    <div class="toast-container position-absolute p-3 m-5 top-0 end-0 ">
      <Toast v-for="(msg, key) in messages" :key="key"
        :msg="msg"
      />
    </div>
  </template>
  
  <script>
  import Toast from '@/components/back/Toast.vue';
  export default {
    components: { Toast },
    data() {
      return {
        messages: [],
      };
    },
    inject: ['emitter'],
    mounted() {
      this.emitter.on('push-message', (message) => {
        const { style = 'success', title, content } = message;
        this.messages.push({ style, title, content });
      });
    },
  };
  </script>